.tickets {
  background:
    url("../img/background/weights-desktop.png") no-repeat right 110%,
    linear-gradient($color-ghost-white 87.64%, $color-default-white 87.64%);
  padding: 120px 0 240px 0;

  @media (max-width: $tablet-width) {
    padding: 100px 0 200px 0;
    background:
      url("../img/background/weights-tablet.png") no-repeat right 100%,
      linear-gradient($color-ghost-white 94.678%, $color-default-white 94.678%);
  }

  @media (max-width: $mobile-width) {
    padding: 70px 0 140px 0;
    background: linear-gradient($color-ghost-white 95.5%, $color-default-white 95.5%);
  }
}

.tickets h2 {
  margin: 0;
  text-transform: uppercase;
  font-family: $font-bebas-neue;
  font-size: 40px;
  line-height: 49px;
  text-align: center;
  margin-bottom: 50px;

  @media (max-width: $tablet-width) {
    font-size: 42px;
    line-height: 42px;
    margin-bottom: 30px;
  }

  @media (max-width: $mobile-width) {
    font-size: 32px;
    line-height: 32px;
    margin-bottom: 30px;
  }
}
